import { Card, CardActionArea, CardContent, CardMedia, Grid, Skeleton, Typography } from '@mui/material'
import React, { useEffect, useState } from 'react'

type props = {
  setIsOpen: Function,
  charaDetail: any,
  setDetail: any
}

const CharacterCard = ({ setIsOpen, charaDetail, setDetail }: props) => {

  // 
  const [isLoad, setIsLoad] = useState(false);

  // 
  const handleLoad = () => {
    setIsLoad(true);
  }
  useEffect(() => {
    setIsLoad(false);
  }, [charaDetail])

  // 
  const handleClick = () => {
    setIsOpen(true);
    setDetail(charaDetail);
  }

  return (
    <Grid item xs={2.4}>
      <Card>
        <CardActionArea
          onClick={handleClick}
        >
          {// 
          isLoad ? (<CardMedia
            component="img"
            alt="media"
            image={charaDetail.image.icon}
            height="170"
            onLoad={handleLoad}
          />
          ) : (
            <>
              <Skeleton variant='rectangular' height={170} animation='wave' />
              <CardMedia
                component="img"
                alt="media"
                image={charaDetail.image.icon}
                height="170"
                onLoad={handleLoad}
                sx={{ display: 'none' }}
              />
            </>
          )
          }
          <CardContent sx={{ pt: 1, pb: 1 }}>
            <Typography variant='subtitle1' color='text.secondary' sx={{fontWeight:'bold'}}>
              {charaDetail.character.name}
            </Typography>
          </CardContent>
        </CardActionArea>
      </Card>
    </Grid>
  )
}

export default CharacterCard