	// 
    getBulletType(type: string) {
		let result: string;
		if(type === 'Explosive') {
			result = '';
		} else if(type === 'Piercing') {
			result = '';
		} else if(type === 'Mystic') {
			result = '';
		} else if(type === 'Sonic') {
			result = ''
		} else {
            result = ''
        }
        return result;
	}

	// 
    getBulletTypeStyle(type: string): 'primary' | 'secondary' | 'error' | 'info' | 'success' | 'warning' {
        let result: 'primary' | 'secondary' | 'error' | 'info' | 'success' | 'warning';
        if(type === 'Explosive') {
			result = 'error';
		} else if(type === 'Piercing') {
			result = 'info';
		} else if(type === 'Mystic') {
			result = 'success';
		} else if(type === 'Sonic') {
			result = 'warning';
		} else {
            result = 'primary';
        }
        return result;
    }

